a,
.control {
  color: #444;
}
.bfloat > svg {
  fill: #444;
}
.bmodal hr {
  border-top: 1px solid #444;
}
#banner {
  background: rgba(221, 221, 221, 0.7);
  border-bottom: 1px solid #ccc;
}
b {
  color: #444;
}
b.admin {
  color: #f00000;
}
b.moderator {
  color: purple;
}
a:hover,
blockquote a {
  color: #666;
}
article,
aside,
.pagination {
  background-color: #ddd;
  border-right: 1px solid #ccc;
  border-bottom: 1px solid #ccc;
}
article.highlight {
  background-color: #ccc;
  border-color: #bbb;
}
article.editing {
  background-color: #fa6;
  border-color: #e95;
}
body {
  background-color: #f0f0f0;
  color: #222;
}
body,
pre,
.themed {
  font-family: Arial, Helvetica, sans-serif;
  font-size: 10pt;
}
em {
  color: #888962;
}
h1 {
  color: #999;
  font: bolder 28px Tahoma;
  letter-spacing: -2px;
}
h2 {
  color: #999;
  font-family: Tahoma;
}
header nav a {
  color: black;
}
hr {
  border-top: 1px solid #ccc;
}
.popup-menu li:hover {
  background-color: #f5f5f5;
}
.email {
  color: #0f0c5d;
}
.modal,
.bmodal,
.preview,
.popup-menu {
  background-color: white;
  border-right: 1px solid #eee;
  border-bottom: 1px solid #eee;
}
.omit {
  color: gray;
}
